.aboutMeCon2 {
  img {
    width: 100%;
    height: 94vh;
    object-fit: cover;
    animation: down 2s;
    transition: 2s ease-in-out;
    z-index: -1;
  }

  @keyframes down {
    0% {
      transform: translateY(-100%);
    }
    100% {
      transform: translateY(0);
    }
  }
}
